	module.exports = {"data":{"markdownRemark":{"html":"<h1>For Loop</h1>\n<p>The <code>for</code> loop executes a block of code until a specified condition is false. Use <code>while</code> loops when the number of iterations are variable, otherwise use <code>for</code> loops. A common use of <code>for</code> loops are array iterations.</p>\n<h2>Example</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>#include <stdio.h>\n\nint main () {\n\n    int i;\n    int array[] = {1, 2, 3, 4, 5};\n\n    for (int i = 0; i < 5; i++) {\n    \tprintf(\"Item on index %d is %d\\n\", i, array[i]);\n    }\n}</code></pre>\n      </div>\n<h2>Output:</h2>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>> Item on index 0 is 1\n> Item on index 1 is 2\n> Item on index 2 is 3\n> Item on index 3 is 4</code></pre>\n      </div>","fields":{"slug":"/c/for/"},"frontmatter":{"title":"For Loop"}}},"pathContext":{"slug":"/c/for/"}}

